<template>
  <div class="line_norikae c">
    <div class="titleArea r" v-if="props.title || props.url">
      <line_text_icon_link
        style="flex: 1; text-align: center"
        :text="props.title"
        :url="props.url"
      ></line_text_icon_link>
    </div>
    <div
      class="timeDiffRow"
      v-if="getTimeDiff(props.departure.time, props.arrive.time)||props.type"
    >
      <div class="timeDiffArea">
        {{ getTimeDiff(props.departure.time, props.arrive.time) }}
      </div>
      <div class="typeArea" v-if="props.type">
        {{ props.type }}
      </div>
    </div>
    <div class="locationArea r">
      <div class="f c">
        <line2x_icon
          class="location"
          :title="getTime(props.departure.time)"
          :subTitle="props.departure.name"
          :iconType="props.departure.iconType"
          :memo="props.departure.memo"
        >
        </line2x_icon>
      </div>

      <div class="f c" v-if="props.transfers && props.transfers.length > 0">
        <line2x_icon
          class="location"
          v-for="transfer in props.transfers"
          :key="transfer.name"
          :title="getTime(transfer.time)"
          :subTitle="transfer.name"
          :iconType="transfer.iconType"
          :memo="transfer.memo"
        ></line2x_icon>
      </div>

      <div class="f c">
        <line2x_icon
          class="location"
          :title="getTime(props.arrive.time)"
          :subTitle="props.arrive.name"
          :iconType="props.arrive.iconType"
          :memo="props.arrive.memo"
          :alert="getDayDiff(props.departure.time, props.arrive.time)"
        ></line2x_icon>
      </div>
    </div>
  </div>
</template>

<script>
import line2x_icon from "./line2x_icon.vue";
import line_text_icon_link from "./line_text_icon_link.vue";
export default {
  components: { line2x_icon, line_text_icon_link },
  props: {
    departure: { time: "", name: "", iconType: "" },
    transfers: Array,
    arrive: { time: "", name: "", iconType: "" },
    title: String,
    url: String,
    type: String,
  },
  setup(props) {
    return { props };
  },
  methods: {
    getTime(timeStamp) {
      if (!timeStamp) return;
      var date = new Date(timeStamp);
      return (
        String(date.getHours()).padStart(2, "0") +
        ":" +
        String(date.getMinutes()).padStart(2, "0")
      );
    },
    getDayDiff(date_beg, date_end) {
      var dateTime_beg = new Date(date_beg);
      var dateTime_end = new Date(date_end);
      var beg =
        dateTime_beg.getFullYear() +
        "-" +
        (dateTime_beg.getMonth() + 1) +
        "-" +
        dateTime_beg.getDate() +
        "T00:00:00";
      var end =
        dateTime_end.getFullYear() +
        "-" +
        (dateTime_end.getMonth() + 1) +
        "-" +
        dateTime_end.getDate() +
        "T00:00:00";
      var timeGap = new Date(end).getTime() - new Date(beg).getTime();
      var dayDiff = Math.floor(timeGap / (3600 * 1000 * 24));
      if (dayDiff > 0) {
        return "+" + dayDiff.toString();
      } else if (dayDiff < 0) {
        return dayDiff.toString();
      } else {
        return;
      }
    },
    getTimeDiff(date_beg, date_end) {
      var timeGap = new Date(date_end).getTime() - new Date(date_beg).getTime();
      var dayDiff = Math.floor(timeGap / (3600 * 1000 * 24));
      var leave = timeGap % (3600 * 1000 * 24);
      var hourDiff = Math.floor(leave / (3600 * 1000));
      var leave1 = leave % (3600 * 1000);
      var minDiff = Math.floor(leave1 / (60 * 1000));
      var res = [];
      if (dayDiff > 0) {
        res.push(dayDiff + "天");
      }
      if (hourDiff > 0) {
        res.push(hourDiff + "時");
      }
      if (minDiff > 0) {
        res.push(minDiff + "分");
      }
      console.log(res.join(""));
      if (res.length > 0) {
        return res.join("");
      } else {
        return;
      }
    },
  },
};
</script>

<style scoped>
.line_norikae {
  display: flex;
  z-index: 3;
  margin: 8px 0px;
}
.titleArea {
  display: flex;
  font-size: 1.25rem;
  font-weight: 500;
  text-align: center;
  position: relative;
}
.locationArea {
  display: flex;
  flex: 1;
  margin: 8px 0px;
}
.f {
  flex: 1;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.f:not(:last-child) .location::after {
  content: "";
  position: absolute;
  top: 50%;
  background: var(--dark-blue-3f);
  width: calc(50% - 156px / 2);
  height: 5px;
  right: 0px;
}
.f:not(:first-child) .location:before {
  content: "";
  position: absolute;
  top: 50%;
  background: var(--dark-blue-3f);
  width: calc(50% - 156px / 2);
  height: 5px;
  left: 0px;
}
.location:first-child {
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}
.location:last-child {
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}
.timeDiffRow {
  display: inline-flex;
  position: relative;
  margin: 8px 0;
}
.timeDiffRow > div {
  background-color: var(--light-white-6c);
  border-radius: 8px;
  padding: 4px 12px;
  font-size: 1rem;
  letter-spacing: 2px;
  font-weight: 700;
  position: absolute;
}
.timeDiffArea {
  right: 0;
}

@media screen and (max-width: 768px) {
  .locationArea.r {
    flex-direction: column;
  }
  .f {
    margin: 16px auto;
  }
  .f:not(:last-child) .location::after {
    bottom: auto;
    top: 100%;
    right: 50%;
    left: auto;
    width: 5px;
    height: 16px;
  }
  .f:not(:first-child) .location::before {
    bottom: 100%;
    top: auto;
    right: 50%;
    left: auto;
    width: 5px;
    height: 16px;
  }
}
</style>
