<template>
  <div class="panel_dayChange r">
    <div class="dayMark">
      <line2x_icon
        :title="props.day1.title"
        :subTitle="getDate(props.day1.date)"
        style="border-radius: 8px"
      ></line2x_icon>
    </div>

    <div class="imgArea">
      <div class="imgArea"  v-if="props.day1.title!=='Day Now'"> 
        <img :src="getImage(props.dayIndex)" />
      </div>
    </div>
    <div class="dayMark">
      <line2x_icon
        :title="props.day2.title"
        :subTitle="getDate(props.day2.date)"
        style="border-radius: 8px"
      ></line2x_icon>
    </div>
  </div>
</template>

<script>
import line2x_icon from "./line2x_icon.vue";
export default {
  components: {
    line2x_icon,
  },
  props: {
    dayIndex: Number,
    day1: {
      title: String,
      date: String,
    },
    day2: {
      title: String,
      date: String,
    },
  },
  setup(props) {
    return { props };
  },
  methods: {
    getDate(timeStamp){
      if (!timeStamp) return;
      const day_list = ['SUN', 'MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT'];
      var date = new Date(timeStamp);
      return date.getFullYear()+'-'+String(date.getMonth() + 1).padStart(2, "0")+'-'+String(date.getDate()).padStart(2, "0")+' '+ day_list[date.getDay()];
    },
    getTime(timeStamp) {
      if (!timeStamp) return;
      var date = new Date(timeStamp);
      return (
        String(date.getHours()).padStart(2, "0") +
        ":" +
        String(date.getMinutes()).padStart(2, "0")
      );
    },
    getImage(dayIndex) {
      const files = require.context(
        "@/assets/imgs/changeDay",
        false,
        /\.(svg|png|jpg|jpeg)$/
      );
      return files(files.keys()[dayIndex % files.keys().length]);
    },
  },
};
</script>

<style scoped>
.panel_dayChange {
  display: flex;
  box-sizing: border-box;
}
.dayMark {
  border-radius: 8px;
  flex: 1;
  max-width: 160px;
  display: flex;
  align-items: flex-start;
}
.dayMark:last-child {
  align-items: flex-end;
  justify-content: flex-end;
}
.imgArea {
  flex: 1;
  display: inline-block;
  line-height: 0px;
}
.imgArea > img {
  height: 100%;
  width: 100%;
  min-width: 220px;
  min-height: 100px;
  max-height: 150px;
  border-radius: 8px;
  background-color: var(--light-white-6c);
  padding: 4px;
}
@media screen and (max-width: 768px) {
  .panel_dayChange.r {
    flex-direction: column;
    align-items: center;
  }
  .panel_dayChange > div {
    padding: 8px;
  }
}
</style>