<template>
  <div class="panel_analyze c">
    <div class="r1 r">
      <card_percent
        v-for="item in allCostTimeItems"
        :key="item.key"
        :percent="item.percent"
        :subTitle1="item.subTitle1"
        :subTitle2="item.subTitle2"
        :title="item.title"
      ></card_percent>
    </div>
    <div class="r1">
      <card_percent_bar :exchangeRates="exchangeRates" :items="allCostItems"></card_percent_bar>
    </div>
  </div>
</template>

<script>
import card_percent from "./cards/card_percent.vue";
import card_percent_bar from "./cards/card_percent_bar.vue";
import {getCurrentInstance, onMounted } from "vue";
export default {
  components: { card_percent, card_percent_bar },
  props: {
    travalDatas: Object.create(
      Array({
        date: String,
        schedules: Object.create(
          Array({
            title: Object.create({
              iconType: String,
              value: String,
            }),
            tabs: Object.create(
              Array({
                tabName: String,
                components: Object.create(
                  Array({
                    type: String,
                    datas: {},
                  })
                ),
              })
            ),
          })
        ),
      })
    ),
  },
  data() {
    return {
      allCostItems: Array(),
      allCostTimeItems: Array(),
      allCostTimeMins: Number,
      exchangeRates: [
        {
          currency: "日",
          value: 0.22,
        },
      ],
    };
  },
  setup(props) {

    onMounted(() => {
        const ctx = getCurrentInstance().ctx;
        ctx.getAllDatas();
    });
    return { props };
  },
  methods: {
    getAllDatas() {
      let items = [];
      let totalTimeMins = 0;
      let totalTimeMins_items = [];
      this.$props.travalDatas.forEach((travalData) => {
        travalData.schedules.forEach((schedule) => {
          if (
            schedule.tabs.find((tab) => {
              return tab.tabName === "金額";
            })
          ) {
            schedule.tabs
              .find((tab) => {
                return tab.tabName === "金額";
              })
              .components.forEach((component) => {
                if (component.type === "card_amount") {
                  items = items.concat(component.datas.items);
                }
              });
          }
          if (
            schedule.tabs.find((tab) => {
              return tab.tabName === "詳情";
            })
          ) {
            schedule.tabs
              .find((tab) => {
                return tab.tabName === "詳情";
              })
              .components.forEach((component) => {
                if (component.type === "norikae") {
                  var mins = this.getTimeDiff_min(
                    component.datas.departure.time,
                    component.datas.arrive.time
                  );

                  if (component.datas.type) {
                    if (!totalTimeMins_items[component.datas.type]) {
                      totalTimeMins_items[component.datas.type] = [];
                    }
                    totalTimeMins_items[component.datas.type].push({
                      mins: mins,
                    });
                  }
                  totalTimeMins += mins;
                }
              });
          }
        });
      });
      this.$data.allCostItems = items;
      this.$data.allCostTimeMins = totalTimeMins;
      this.$data.allCostTimeItems = [];
      for (const [key, value] of Object.entries(totalTimeMins_items)) {
        let totalMin = value.map((el) => el.mins).reduce((a, b) => a + b);
        let percent = (totalMin * 100) / totalTimeMins;
        var hourDiff = Math.floor(totalMin / 60);
        var minDiff = totalMin % 60;
        let subTitle1 =
          totalMin >= 60
            ? hourDiff + "小時" + minDiff + "分鐘"
            : minDiff + "分鐘";
        let subTitle2 = "總共 " + value.length + "個行程";
        this.$data.allCostTimeItems.push({
          title: key,
          percent,
          subTitle1,
          subTitle2,
        });
      }
    },
    getTimeDiff_min(date_beg, date_end) {
      var timeGap = new Date(date_end).getTime() - new Date(date_beg).getTime();
      var timeGapMin = Math.floor(timeGap / (60 * 1000));
      return timeGapMin || 0;
    },
  },
};
</script>

<style scoped>
.panel_analyze {
  display: flex;
}
.r1 {
  flex: 1;
  display: flex;
}
.r1 > div {
  margin-left: 4px;
  margin-right: 4px;
  flex: 1;
}
.r1 > div:first-child {
  margin-left: 0px;
}
.r1 > div:last-child {
  margin-right: 0px;
}
</style>