<template>
  <div class="line_text_icon_link r">
    <div class="text">
      {{ props.text }}
    </div>
    <div class="iconArea" v-if="props.url" @click="openUrl(props.url)">
      <externalLinkIcon></externalLinkIcon>
    </div>
  </div>
</template>

<script>
import { ExternalLink as externalLinkIcon } from "@vicons/tabler";
export default {
  components: { externalLinkIcon },
  props: {
    text: String,
    url: String,
  },
  setup(props) {
    return { props };
  },
  methods: {
    openUrl(url) {
      window.open(url);
    },
  },
};
</script>

<style scoped>
.line_text_icon_link {
  display: flex;
  margin: 8px 0px;
  font-size: 1rem;
  font-weight: 500;
  text-align: center;
  align-items: center;
  text-align: left;
}
.text {
  background-color: var(--light-white-6c);
  border-radius: 8px;
  flex: 1;
  padding: 0.5rem 0.8rem;
}
.iconArea {
  width: 1.5rem;
  height: 1.5rem;
  background-color: var(--light-white-6c);
  border-radius: 8px;
  padding: 0.5rem;
  margin-left: 8px;
  cursor: pointer;
}
</style>