<template>
  <div class="base">
    <div style="display: flex; margin-top: 8px" class="container c card radus">
      <line_icon_title
        :title="getCover().title.value"
        :iconType="getCover().title.iconType"
      ></line_icon_title>
      <n-tabs
        class="custom-tabs"
        :type="'line'"
        justify-content="space-evenly"
        :pane-style="{ padding: '0px' }"
        :tab-style="{
          color: 'var( --dark-blue-3f)',
          fontSize: '1.2rem',
          fontWeight: 700,
        }"
        animated
      >
        <n-tab-pane :name="'封面'" :tab="'封面'">
          <div v-for="component in getCover().components" :key="component">
            <line_banner
              v-if="component.type === 'banner'"
              :src="component.datas.img"
            ></line_banner>
          </div>
        </n-tab-pane>
        <n-tab-pane :name="'分析'" :tab="'分析'">
          <panel_analyze :travalDatas="getData()"></panel_analyze>
        </n-tab-pane>
      </n-tabs>
    </div>

    <panel_dayChange
      :day1="getDayChange(-1).day1"
      :day2="getDayChange(-1).day2"
      :dayIndex="0"
      style="margin-top: 8px"
    >
    </panel_dayChange>

    <div v-for="(day, dayIndex) in getData()" :key="dayIndex">
      <div
        style="display: flex; margin-top: 8px"
        class="container c card radus"
        v-for="schedule in day.schedules"
        :key="schedule"
      >
        <line_icon_title
          :title="schedule.title.value"
          :iconType="schedule.title.iconType"
        ></line_icon_title>
        <n-tabs
          class="custom-tabs"
          :type="'line'"
          justify-content="space-evenly"
          :pane-style="{ padding: '0px' }"
          :tab-style="{
            color: 'var( --dark-blue-3f)',
            fontSize: '1.2rem',
            fontWeight: 700,
          }"
          animated
        >
          <n-tab-pane
            v-for="tab in schedule.tabs"
            :key="tab.tabName"
            :name="tab.tabName"
            :tab="tab.tabName"
          >
            <div v-for="component in tab.components" :key="component">
              <line_norikae
                v-if="component.type === 'norikae'"
                :title="component.datas.title"
                :url="component.datas.url"
                :type="component.datas.type"
                :departure="component.datas.departure"
                :transfers="component.datas.transfers"
                :arrive="component.datas.arrive"
              ></line_norikae>
              <line_photo_4x
                v-if="component.type === 'photo4x'"
                :srcs="component.datas.imgs"
                :title="component.datas.title"
                style="margin-top: 8px"
              ></line_photo_4x>
              <line_text
                v-if="component.type === 'text'"
                :texts="component.datas.contents"
                style="margin-top: 8px"
              ></line_text>
              <card_amount
                v-if="component.type === 'card_amount'"
                :items="component.datas.items"
              >
              </card_amount>
              <line_text_icon_link
                v-if="component.type === 'text_link'"
                :text="component.datas.text"
                :url="component.datas.url"
              ></line_text_icon_link>
              <line_banner
                v-if="component.type === 'banner'"
                :src="component.datas.img"
              ></line_banner>
            </div>
          </n-tab-pane>
        </n-tabs>
      </div>
      <panel_dayChange
        :day1="getDayChange(dayIndex).day1"
        :day2="getDayChange(dayIndex).day2"
        :dayIndex="dayIndex"
        style="margin-top: 8px"
      >
      </panel_dayChange>
    </div>
  </div>
</template>

<script>
import { NTabs, NTabPane } from "naive-ui";
import allData from "@/database/data.json";
import line_icon_title from "./components/line_icon_title.vue";
import line_norikae from "./components/line_norikae.vue";
import line_photo_4x from "./components/line_photo_4x.vue";
import line_text from "./components/line_text.vue";
import card_amount from "./components/cards/card_amount.vue";
import panel_dayChange from "./components/panel_dayChange.vue";
import line_text_icon_link from "./components/line_text_icon_link.vue";
import line_banner from "./components/line_banner.vue";
import panel_analyze from "./components/panel_analyze.vue";
export default {
  name: "App",
  components: {
    line_icon_title,
    line_norikae,
    line_photo_4x,
    line_text,
    card_amount,
    panel_dayChange,
    line_text_icon_link,
    line_banner,
    panel_analyze,
    NTabs,
    NTabPane,
  },
  data() {
    return {};
  },
  methods: {
    getCover() {
      return allData.cover;
    },
    getData() {
      return allData.travalDatas;
    },
    getDayChange(dayIndex) {
      var res = {};
      if (dayIndex < 0) {
        res.day1 = {
          title: "Day Now",
          date: new Date().toJSON(),
        };
      } else {
        res.day1 = {
          title: "Day" + (dayIndex + 1),
          date: this.getData()[dayIndex].date,
        };
      }
      if (dayIndex < this.getData().length - 1) {
        res.day2 = {
          title: "Day" + (dayIndex + 2),
          date: this.getData()[dayIndex + 1].date,
        };
      } else {
        res.day2 = {
          title: "End",
          date: "Happy End",
        };
      }
      return res;
    },
  },
};
</script>

