<template>
  <div class="card_percent_bar c" v-if="props.items && props.items.length > 0">
    <div class="totalCostArea c">
      <div class="titleArea">
        <line_icon_title class="title" :title="'總共開銷'"></line_icon_title>
      </div>
      <div class="dataArea r">
        <div class="iconArea">
          <CurrencyDollarIcon class="icon"></CurrencyDollarIcon>
        </div>
        <div class="valueArea c">
          <line_cost_count
            class="mainValue"
            :item="{ name: '', amount: countAllCost(props.items, props.exchangeRates), currency: '台' }"
          ></line_cost_count>
          <div class="memo"></div>
        </div>
      </div>
    </div>

    <div class="mainArea c">
      <line_cost_progressBar
        v-for="item in countAllPercent(props.items, props.exchangeRates)"
        :key="item.currency"
        :type="item.type"
        :percent="roundToTwo(item.percent)"
        :value="item.value"
      ></line_cost_progressBar>
    </div>
  </div>
</template>

<script>
import line_icon_title from "../line_icon_title.vue";
import line_cost_progressBar from "../line_cost_progressBar.vue";
import line_cost_count from "../line_cost_count.vue";
import { CurrencyDollar as CurrencyDollarIcon } from "@vicons/tabler";
export default {
  components: {
    line_icon_title,
    line_cost_progressBar,
    line_cost_count,
    CurrencyDollarIcon,
  },
  props: {
    items: Object.create(
      Array({
        type: String,
        name: String,
        amount: Number,
        currency: String,
        payed: Boolean,
      })
    ),
    exchangeRates: Object.create(
      Array({
        currency: String,
        value: Number,
      })
    ),
  },
  setup(props) {
    return { props };
  },
  methods: {
    countAllCost(items, exchangeRates){
        let total = 0;
        console.log('items',items);
        items.forEach((element) => {       
        var amount =
          element.currency === "台"
            ? element.amount
            : element.amount *
              exchangeRates.find((rate) => {
                return rate.currency === element.currency;
              }).value;
        total += 0 || amount;
      });
      return total.toFixed(2);
    },
    countAllPercent(items, exchangeRates) {
      let counter = {};
      let total = 0;
      let res = [];

      items.forEach((element) => {
        var amount =
          element.currency === "台"
            ? element.amount
            : element.amount *
              exchangeRates.find((rate) => {
                return rate.currency === element.currency;
              }).value;
        counter[element.type] = counter[element.type] + amount || 0 + amount;
        total += 0 || amount;
      });

      for (const [key, value] of Object.entries(counter)) {
        var percent = (value * 100) / total || 0.0;
        res.push({ type: key, percent: percent, value: value });
      }
      return res;
    },
    roundToTwo(num) {
    return +(Math.round(num + "e+2")  + "e-2");
}
  },
};
</script>

<style scoped>
.card_percent_bar {
  display: flex;
}
.title {
  font-size: 1.3rem;
}
.mainArea {
  display: flex;
}
.mainArea > div {
  display: flex;
}
.barArea {
  flex: 1;
  display: flex;
  align-items: center;
}
.totalCostArea {
  display: flex;
  background-color: var(--light-white-6c);
  width: 50%;
  border-radius: 8px;
  margin: 12px auto;
  padding: 8px;
  box-sizing: border-box;
}
.dataArea {
  display: flex;
  flex-wrap:  wrap;
  justify-content: center;
}
.iconArea {
  display: flex;
  align-items: center;
  justify-content: center;
}
.icon {
  width: 4.2rem;
  height: 4.2rem;
}
.valueArea {
  display: flex;
  flex: 1;
}
.mainValue {
  font-size: 3rem;
  font-weight: 700;
  background-color: transparent;
}
@media screen and (max-width: 768px) {
    .totalCostArea{
        width: 100%;
    }
}
</style>