<template>
  <div class="panel_cost_count">
    <line_cost_count
      v-for="item in items"
      :key="item.name"
      :item="item"
    ></line_cost_count>
    <div class="line"></div>
    <line_cost_count
      v-for="it in countTotal(props.items)"
      :key="it.currency"
      :item="it"
    ></line_cost_count>
  </div>
</template>

<script>
import line_cost_count from "./line_cost_count.vue";

export default {
  components: { line_cost_count },
  emits: ["countTotal", "countPayed", "countYetPayed"],
  props: {
    items: Object.create(
      Array({
        type: String,
        name: String,
        amount: Number,
        currency: String,
        payed: Boolean,
      })
    ),
  },
  setup(props) {
    return {
      props,
    };
  },
  methods: {
    countTotal(datas) {
      var res = this.count(datas);
      this.$emit("countTotal", res);
      this.$emit("countPayed", this.count(datas.filter((e) => e.payed)));
      this.$emit("countYetPayed", this.count(datas.filter((e) => !e.payed)));
      console.log("pp", this.count(datas.filter((e) => e.payed)));
      return res;
    },
    count(datas) {
      let counter = {};
      let res = [];
      datas.forEach((element) => {
        counter[element.currency] =
          counter[element.currency] + element.amount || 0 + element.amount;
      });

      for (const [key, value] of Object.entries(counter)) {
        res.push({ type: "", name: "Total", amount: value, currency: key });
      }
      return res;
    },
  },
};
</script>

<style scoped>
.panel_cost_count {
  border-radius: 8px;
  overflow: hidden;
}
.line {
  height: 4px;
  width: 90%;
  background-color: var(--dark-blue-3f);
  border-radius: 50px;
  margin: 4px auto;
}
</style>