<template>
  <div class="container c">
    <div style="margin-top: 8px">
      <panel_cost_title :items="total" :title="'全部'"></panel_cost_title>
    </div>

    <div class="overviewArea r">
      <panel_cost_title :items="totalPayed" :title="'已付'"></panel_cost_title>
      <panel_cost_title :items="yetPayed" :title="'未付'"></panel_cost_title>
    </div>
  </div>

  <n-tabs
    :bar-width="28"
    :type="'line'"
    animated
    :placement="'top'"
    :justify-content="'center'"
    :tab-style="{
      color: 'var( --dark-blue-3f)',
      fontWeight: 700,
    }"
  >
    <n-tab-pane name="total" tab="全部">
      <panel_cost_count
        :items="props.items"
        @countPayed="countPayed"
        @countTotal="countTotle"
        @countYetPayed="countYetPayed"
      ></panel_cost_count>
    </n-tab-pane>
    <n-tab-pane name="payed" tab="已付">
      <panel_cost_count
        :items="props.items.filter((e) => e.payed)"
      ></panel_cost_count>
    </n-tab-pane>
    <n-tab-pane name="yetPay" tab="未付">
      <panel_cost_count
        :items="props.items.filter((e) => !e.payed)"
      ></panel_cost_count>
    </n-tab-pane>
  </n-tabs>
</template>

<script>
import { NTabPane, NTabs } from "naive-ui";
import panel_cost_count from "../panel_cost_count.vue";
import panel_cost_title from "../panel_cost_title.vue";
export default {
  components: { panel_cost_count, panel_cost_title, NTabPane, NTabs },
  props: {
    items: Object.create(
      Array({
        type: String,
        name: String,
        amount: Number,
        currency: String,
        payed: Boolean,
      })
    ),
  },
  data() {
    return {
      totalPayed: [],
      total: [],
      yetPayed: [],
    };
  },
  setup(props) {
    console.log('prprprp',props);
    return { props };
  },
  methods: {
    countTotle(res) {
      this.total = res;
    },
    countPayed(res) {
      this.totalPayed = res;
    },
    countYetPayed(res) {
      this.yetPayed = res;
    },
  },
};
</script>

<style scoped>
.container {
  display: flex;
}
.overviewArea {
  display: flex;
  margin: 8px 0px;
}
.overviewArea > div {
  flex: 1;
}
.overviewArea > div:first-child {
  margin-right: 4px;
}
.overviewArea > div:last-child {
  margin-left: 4px;
}
</style>