<template>
  <div class="card_percent r">
    <n-progress
      type="dashboard"
      gap-position="bottom"
      :percentage="roundToTwo(percent)"
    />
    <div class="textArea c">
      <line_icon_title :title="title" class="mainTitle"></line_icon_title>
      <div class="subTitle">
        <div>{{ subTitle1 }}</div>
      </div>
      <div class="subTitle">
        <div>{{ subTitle2 }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import line_icon_title from "../line_icon_title.vue";
import { NProgress } from "naive-ui";
export default {
  components: { line_icon_title, NProgress },
  props: {
    title: String,
    percent: Number,
    subTitle1: String,
    subTitle2: String,
  },
  methods: {
    roundToTwo(num) {
      return +(Math.round(num + "e+2") + "e-2");
    },
  },
};
</script>

<style scoped>
.card_percent {
  display: flex;
  width: 100%;
  background-color: var(--light-white-6c);
  padding: 8px;
  border-radius: 8px;
}
.textArea {
  flex: 1;
  display: flex;
}
.mainTitle {
  font-size: 1.3rem;
}
.subTitle {
  font-weight: 500;
}
.subTitle:last-child {
  flex: 1;
  display: inline-flex;
  align-content: flex-end;
  justify-content: flex-end;
}

@media screen and (max-width: 768px) {
  .card_percent.r{
    flex-direction: column;
    align-content: center;
    align-items: center;
  }

}
</style>