<template>
  <div class="bannerArea">
    <img  object-fit="cover" :src="formateSrc(src)"  alt="">
  </div>
</template>

<script>
export default {
  props: { src: String },
  setup(props) {
    return { props };
  },
  methods: {
    formateSrc(src) {
      return process.env.NODE_ENV === "production"
        ? "/trip2shigoku" + src //github專案名稱
        : src;
    },
  },
};
</script>

<style>
.bannerArea>img{
    width: 100%;
    height: 100%;
}
</style>