<template>
  <div class="container c">
    <div class="titleArea">
      <div class="title">{{ props.title }}</div>
    </div>
    <div class="imgArea r">
      <n-image-group>
        <div class="cell c" v-for="src in props.srcs" :key="src">
          <div class="area">
            <n-image class="myImg" object-fit="cover" :src="formateSrc(src)" />
          </div>
        </div>
      </n-image-group>
    </div>
  </div>
</template>

<script>
import { NImage, NImageGroup } from "naive-ui";
export default {
  components: {
    NImage,
    NImageGroup,
  },
  props: { srcs: Array, title: String },
  setup(props) {
    return { props };
  },
  methods: {
    formateSrc(src) {
      return process.env.NODE_ENV === "production"
        ? "/trip2shigoku" + src //github專案名稱
        : src;
    },
  },
};
</script>

<style scoped>
.container {
  flex: 1;
  display: flex;
  /* max-height: 300px; */
  background-color: var(--light-white-6c);
  border-radius: 8px;
}
.cell {
  flex: 1;
  display: inline-block;
  margin: 4px;
  display: flex;
}
.area {
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  border-radius: 8px;
  overflow: hidden;
  max-height: 300px;
}
.titleArea {
  display: flex;
}
.title {
  font-size: 1rem;
  font-weight: 500;
  padding: 4px 12px 0px 12px;
  align-content: center;
}
.imgArea {
  display: flex;
}
.myImg {
  height: 100%;
  width: 100%;
}
</style>