<template>
  <div class="line_cost_count c">
    <div class="item r">
      <div class="typeArea" v-show="item.type">
        <div class="type tag">{{ item.type }}</div>
      </div>
      <div class="nameArea">
        <div class="name tag">{{ item.name }}</div>
      </div>
      <div class="amountArea">
        <div class="amount tag">{{ numberFormate(item.amount) }}</div>
      </div>
      <div class="currency tag" v-if="item.currency">{{ item.currency }}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    item:Object.create({
      type: String,
      name: String,
      amount: Number,
      currency: String,
    }),
  },
  setup(props) {
    return { props};
  },
  methods: {
    numberFormate(num) {
      let comma = /\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g;
      return num.toString().replace(comma, ",");
    },
  },
};
</script>

<style scoped>
.line_cost_count {
  display: flex;
  box-sizing: content-box;
  font-weight: 500;
  font-size: 1rem;
  background-color: var(--light-white-6c);
}
.item {
  display: flex;
}
.tag {
  margin: 2px;
  padding: 4px 8px;
  box-sizing: content-box;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nameArea {
  flex: 1;
  display: flex;
}
.name {
  justify-content: left;
}
.typeArea {
  display: flex;
}
.type {
  min-width: 32px;
  width: 32px;
  position: relative;
}
.type::before{
  content: '';
  width: 4px;
  height: 80%;
  background-color:var(--dark-blue-3f);
  position: absolute;
  right: 0px;
  border-radius: 50px;
}
.amountArea {
  display: flex;
  justify-content: right;
  align-items: center;
}
.amount {
  min-width: 50px;
  height: 28px;
  font-weight: 700;
  justify-content: right;
}
.currency {
  position: relative;
}
.currency::before{
  content: '';
  width: 4px;
  height: 80%;
  background-color:var(--dark-blue-3f);
  position: absolute;
  left: 0px;
  border-radius: 50px;
}
</style>